#bird {
  border-radius: 50%;
  background-color: white;
  width: 1.5em;
  height: 1.5em;
  position: absolute;
  top: 250px;
  left: 40px;
  margin-bottom: 20px;
}

body {
  background-color: #263238;
  height: 80vh;
  font-family: 'Noto Sans', sans-serif;
}
#score {
  font-size: 3.5em;
  color: #f44336;
  position: relative;
  font-weight: bold;
  text-align: center;
  z-index: 15;
}
#gameOver {
  font-size: 3em;
  color: #03A9F4;
  position: relative;
  font-weight: bold;
  top: 150px;
  text-align: center;
  z-index: 15;
  width: 30%;
  height: 170px;
  margin: 0 auto;
  background-color: #424242;
}

#gameOver div {
  padding-top: 2%;
}

#gameOver button {
  background-color: #009688;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  margin-bottom: 2%;
}
#startScreen {
  color: #03A9F4;
  font-size: 2em;
  font-weight: bold;
  width: 100%;
  height: 100%;
  display: table;
  position: absolute;
  text-align: center;
}

#startScreen div {
  display: table-cell;
  vertical-align: middle;
}
